/* Notifications styles */
.Notifications {
    padding: 1rem;
    border: dashed 0.2rem;
    color: crimson;
}

/* Button images */
.close-button {
    float: right;
    border: none;
    background-color: white;
}
.button-img {
    width: 20px;
}

/*list styles*/
.notify-list {
    margin: 2rem 3rem;
    font-size: 1.2rem;
}

[data-priority="default"] {
    color: rgb(13, 13, 116);
}

[data-priority="urgent"] {
    color: red;
}