/* General Styles */
* {
  margin: 0;
  padding: 0;
}

/* Header styles */

.App-header {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: #e1514c;
  font-size: 20px;
  font-weight: bold !important;
  border-bottom: #e1514c solid .3rem;
}

.App-logo {
    width: 15%;
    margin-right: 2%;
}

/* Body content */
.App-body {
  margin: 3rem;
  padding-bottom: 25%;
}

/* inputs */
.form-container {
  flex-direction: row;
  display: flex;
}

.input-container {
  margin-top: 1rem;
  margin-right: 2rem;
}

.input-container input, .input-container button {
  padding: 0.2rem;
  background-color: white;
  border: gray .1rem solid;
}

.input-container label {
  margin-right: 1rem;
}

/* footer content */
.App-footer {
  display: flex;
  justify-content: center;
  padding: 2rem;
  border-top: #e1514c solid .3rem;
}